<template>
  <div class="handlehomework">
    <el-form :model="studenthomework" label-width="100px" class="demo-ruleForm">
      <el-form-item label="学号">
        <el-input v-model.number="studenthomework.studentid"></el-input>
      </el-form-item>
      <el-form-item label="作业ID">
        <el-input v-model.number="studenthomework.homeworkid"></el-input>
      </el-form-item>
      <el-form-item label="作业内容">
        <el-input v-model="studenthomework.homeworkcontent"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="add()">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    props: {
    },
    data(){
      return{
        studenthomework: {
          studentid: '',
          homeworkid: '',
          homeworkcontent:''
        }
      }
    },
    methods:{
      add(){
        let that = this;
        let postData = that.$qs.stringify({
          "studentId":that.studenthomework.studentid,
          "homeworkId":that.studenthomework.homeworkid,
          "homeworkContent":that.studenthomework.homeworkcontent
        });
        that.$axios(
          {
            method: "post",
            url: "/submit",
            data: postData,
          },
        ).then(function(res) {
          alert(res.data.data)
        });
      }
    }
  };
</script>



