<template>
    <div class="student">
      <el-table
        :data="studentList"
        style="width: 600px">
        <el-table-column
          prop="studentId"
          label="学号">
        </el-table-column>
        <el-table-column
          prop="studentName"
          label="姓名">
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
    export default {
        props: {
        },
        data(){
          return{
            studentList: [{
              studentId: null,
              studentName: ''
              }
            ]
          }
        },
        mounted() {
          this.getallstudent();
        },
        methods:{
            getallstudent(){
              let that = this;
              this.$axios(
                {
                  method: "post",
                  url: "/searchstudent",
                  data: null
                },
              ).then(function(res) {
                that.studentList = res.data.data;
              });
            }
        }
    };
</script>



