<template>
  <div class="teacherhomework">
    <el-form :model="homework" label-width="100px" class="demo-ruleForm">
      <el-form-item label="作业ID">
        <el-input v-model.number="homework.homeworkId"></el-input>
      </el-form-item>
      <el-form-item label="作业名">
        <el-input v-model.number="homework.homeworkTitle"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="add()">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    props: {
    },
    data(){
      return{
        homework: {
          homeworkId: '',
          homeworkTitle: ''
        }
      }
    },
    methods:{
      add(){
        let that = this;
        this.$axios(
          {
            method: "post",
            url: "/createHomework",
            data: that.homework
          },
        ).then(function(res) {
          alert(res.data.data)
        });
      }
    }
  };
</script>



