<template>
    <div class="nav" style="display: flex;flex-direction: row">
      <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse">
        <el-menu-item index="2">
          <i v-if="!isCollapse" @click="close" class="el-icon-s-fold"/>
          <i v-else @click="open" class="el-icon-s-unfold"/>
        </el-menu-item>
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-user"></i>
            <span slot="title">学生</span>
          </template>
          <el-menu-item-group>
            <span slot="title"></span>
            <el-menu-item index="1-1" @click="jump('/handlehomework')">提交作业</el-menu-item>
            <el-menu-item index="1-2" @click="jump('/addstudent')">添加学生</el-menu-item>
            <el-menu-item index="1-3" @click="jump('/student')">学生列表</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-s-custom"></i>
            <span slot="title">教师</span>
          </template>
          <el-menu-item-group>
            <span slot="title"></span>
            <el-menu-item @click="jump('/studenthomework')" index="2-1">作业记录</el-menu-item>
            <el-menu-item @click="jump('/teacherhomework')" index="2-2">发布作业</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
      <router-view/>
    </div>
</template>

<script>
    export default {
      data() {
        return {
          isCollapse: true
        };
      },
      methods: {
        close(){
          this.isCollapse = true;
        },
        open(){
          this.isCollapse = false;
        },
        handleOpen(key, keyPath) {
          console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
          console.log(key, keyPath);
        },
        jump(path) {
          this.$router.push({ path: path});
        },
      }
    };
</script>

<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    margin-top: 0px;
    width: 200px;
    min-height: 400px;
    height: 100%;
  }
</style>



