<template>
    <div class="addstudent">
      <el-form :model="student" label-width="100px" class="demo-ruleForm">
        <el-form-item label="学号">
          <el-input v-model.number="student.studentId"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model.number="student.studentName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="add(student)">提交</el-button>
          <el-button @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
    export default {
        data(){
          return{
            student:{
              studentId: "",
              studentName: ""
            }
          }
        },
      methods:{
        add(params){
          let that =this;
          this.$axios({
              //请求方式
              method: "post",
              //请求路劲
              url: "/addstudent",
              //请求参数
              data: params,
              //请求成功的回调函数
              }
            )
            .then(function(res) {
              that.$notify.info({
                title: res.data.msg,
                message: res.data.data
              });
            })
            .catch(function() {
              console.log("服务器异常");
            });
        },
        resetForm(){
          this.student.studentId=null;
          this.student.studentName=null;
        }
      }
    };
</script>



