<template>
    <div class="studenthomework">
      <div>
        <h4>作业提交记录</h4>
      </div>
      <el-table
        :data="studenthomeworkList"
        style="width: 1800px">
        <el-table-column
          prop="id"
          label="编号">
        </el-table-column>
        <el-table-column
          prop="studentId"
          label="学生ID">
        </el-table-column>
        <el-table-column
          prop="homeworkId"
          label="作业ID">
        </el-table-column>
        <el-table-column
          prop="homeworkTitle"
          label="作业名">
        </el-table-column>
        <el-table-column
          prop="homeworkContent"
          label="作业内容">
        </el-table-column>
        <el-table-column
          prop="creatTime"
          label="提交时间">
        </el-table-column>
      </el-table>
      <div>
        <h4>作业列表</h4>
      </div>
      <el-table
        :data="teacherhomeworkList"
        style="width: 1800px">
        <el-table-column
          prop="homeworkId"
          label="作业ID">
        </el-table-column>
        <el-table-column
          prop="homeworkTitle"
          label="作业名">
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
    export default {
      props: {
      },
      data(){
        return{
          studenthomeworkList: [{
            id: 1,
            studentId: 1,
            homeworkId:1,
            homeworkTitle:1,
            homeworkContent:'',
            creatTime:''
            }
          ],
          teacherhomeworkList: [{
            homeworkId:1,
            homeworkTitle:1,
          }
          ]
        }
      },
      mounted() {
        this.getallstudent();
      },
      methods:{
        getallstudent(){
          let that = this;
          this.$axios(
            {
              method: "post",
              url: "/searchallhomework",
              data: null
            },
          ).then(function(res) {
            that.studenthomeworkList = res.data.studenthomework;
            that.teacherhomeworkList = res.data.teacherhomework;
          });
        }
      }
    };
</script>



